<template>
  <section id="uv-search-container">
    <div class="show-uvIndex">
      The UV Index based on your choice:
      <p v-show="uvIndex !== ''">
        {{ 'The avarage UV Index of ' + searchForm.city + ", " + searchForm.state + ", is " + uvIndex }}
      </p>
    </div>
    <el-form
      ref="searchFormRef"
      :model="searchForm"
      :rules="searchFormRules"
      label-width="80px"
      style="width: 95%"
      class="searchSelect"
    >
      <el-form-item label="State: " prop="state">
        <el-select
          v-model="searchForm.state"
          placeholder="pleash select"
          style="width: 100%"
          @change="getCities"
        >
          <el-option
            v-for="(item, index) in stateList"
            :key="index"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="City: " prop="city">
        <el-select
          v-model="searchForm.city"
          placeholder="pleash select"
          style="width: 100%"
        >
          <el-option
            v-for="(item, index) in cityList"
            :key="index"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="Month: " prop="month">
        <el-select
          v-model="searchForm.month"
          placeholder="pleash select"
          style="width: 100%"
        >
          <el-option
            v-for="(item, index) in months"
            :key="index"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <div class="submit-form">
      <el-button @click="cacelSearch" size="small">Cancel</el-button>
      <el-button type="primary" @click="submitSearch" size="small"
        >Submit</el-button
      >
    </div>
  </section>
</template>

<script>
import { getStates, getCities, getUVIndex } from "@/api";
export default {
  // name: "uvLevel",
  data() {
    return {
      uvIndex: "",
      stateList: [],
      cityList: [],
      allCities: [],
      months: [
        {
          label: "January",
          value: "UV_JAN",
        },
        {
          label: "February",
          value: "UV_FEB",
        },
        {
          label: "March",
          value: "UV_MAR",
        },
        {
          label: "April",
          value: "UV_APR",
        },
        {
          label: "May",
          value: "UV_MAY",
        },
        {
          label: "June",
          value: "UV_JUN",
        },
        {
          label: "July",
          value: "UV_JUL",
        },
        {
          label: "Auguest",
          value: "UV_AUG",
        },
        {
          label: "September",
          value: "UV_SEP",
        },
        {
          label: "October",
          value: "UV_OCT",
        },
        {
          label: "November",
          value: "UV_NOV",
        },
        {
          label: "December",
          value: "UV_DEC",
        },
      ],

      searchForm: {
        state: "",
        city: "",
        month: "",
      },
      searchFormRules: {
        state: [
          {
            required: true,
            message: "please select",
            trigger: "blur",
          },
        ],
        city: [
          {
            required: true,
            message: "please select",
            trigger: "blur",
          },
        ],
        month: [
          {
            required: true,
            message: "please select",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    this.getState();
    this.getAllCity();
  },
  methods: {
    async getState() {
      const {data:res} = await getStates();
      if (res) {
        const tempList = [];
        for (let val of res) {
          tempList.push(val.state_name);
        }
        this.stateList = tempList;
      }
    },
    async getAllCity() {
      const {data:res} = await getCities();
      if (res) {
        console.log(res)

        this.allCities = res;
      }
    },
    getCities() {
      let tempCityList = [];
      for (let val of this.allCities) {
        if (this.searchForm.state && this.searchForm.state == val.state_name) {
          tempCityList.push(val.city_name);
        }
      }
      this.cityList = tempCityList;
    },
    cacelSearch() {
      this.searchForm = [];
      this.$refs.searchFormRef.resetFields();
    },
    submitSearch() {
      this.$refs.searchFormRef.validate(async (valid) => {
        if (!valid) return;
        const  {data:res}  = await getUVIndex({
          params: this.searchForm,
        });
        console.log(res);
        if (res) {
          console.log(this.searchForm.month);
          this.uvIndex = res[0][this.searchForm.month];
          console.log(this.uvIndex);
        }
      });
    },
  },
};
</script>

<style scoped>
#uv-search-container {
  background: rgb(196, 238, 174);
  width: 46rem;
  margin-left: 4rem;
  padding: 0.5rem 0;
}

.show-uvIndex {
  font-size: 2rem;
  margin: 1rem 2rem;
}

.el-form {
  margin-top: 2rem;
}

.submit-form {
  margin-bottom: 2rem;
  text-align: center;
}
</style>