import Vue from 'vue'
import VueRouter from 'vue-router'

import Layout from '@/pages/layout'
import Home from '@/pages/home'
import uvLevel from '@/pages/uv-level'
import Sunscreen from '@/pages/sunscreen'
import Sunburn from '@/pages/sunburn'

Vue.use(VueRouter)

export const routes = [
    {
        path: '',
        redirect:'home',
        component: Layout,
        name: 'container',
        children: [
            {
                path: 'home',
                name: 'home',
                component: Home,
            },
            {
                path: 'uvlevel',
                name: 'uvLevel',
                component: uvLevel
            },
            {
                path: 'sunscreen',
                name: 'sunscreen',
                component: Sunscreen
            },
            {
                path: 'sunburn',
                name: 'sunburn',
                component: Sunburn
            },
        ]
    },

]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router