      <h2 id="subTitle-uv">What is UV?</h2>
      <article>
        <p>
          Ultraviolet (UV) radiation is a type of energy that is emitted by the
          sun and some artificial sources, such as arc welders and solariums.
          While it has some benefits for people, including the creation of
          Vitamin D, it also can cause health risks.
        </p>

        <ul>
          <li class="first-li">Natural source of UV radiation:</li>
          <li class="second-li">The sun</li>
          <li class="first-li">Artificial source of UV radiation:</li>
          <li class="second-li">Tanning beds</li>
          <li class="second-li">
