          </li>
          <li class="second-li">
            Some halogen, fluorescent, and incandescent lights
          </li>
          <li class="second-li">Some types of lasers</li>
        </ul>
      </article>

      <h2 id="subTitle-uvIndex">What is UV Index?</h2>
      <article>
        <p>
          The UV Index is the strength of ultraviolet rays at any given time and
          place. It is an important indicator that people need to use sun
          protection. The index changes throughout the day, daily maximum
          usually occurs during the four-hour period around solar noon.
        </p>
        <p>
          Australia is closer to the Equator and it is in the southern
          hemisphere, that is the main reason people living in Australia
          experience higher levels of uv radiation compared with people in North
          America or Europe.
        </p>
        <p>The UV Index divides UV radiation levels into:</p>
        <ul>
          <li class="first-li">low (1-2)</li>
          <li class="first-li">moderate (3-5)</li>
          <li class="first-li">high (6-7)</li>
          <li class="first-li">very high (8-10)</li>
          <li class="first-li">very high (8-10)</li>
        </ul>
      </article>
