      <h2 id="sunburn">What is sunburn?</h2>
      <article>
        <p>
          Have you had a burning sensation on your skin and it feels hot when
          you touch it? And it appears to be red or pink on your skin? That is
          probably sunburn. Sunburn results from too much sunlight exposure, it
          is an inflammatory reaction to the UV which damages the skin. But
          behind it, the main thing that copes with the radiation is Melanin.
          Melanin is a pigment that protects the skin from UV radiation. When
          the radiation is too much that your melanin cannot cope with it, you
          will get sunburn.
        </p>
      </article>

      <h2 id="sunburn-risk">How likely am I to get a sunburn?</h2>
      <article>
        <div class="skin-img">
          <img
            src="@/assets/images/c1ddc75077149b224a2022147b74033.png"
            alt="skin type"
          />
        </div>

        <p>
          Skin type is one of the determining factors of how sensitive you are
          to UV. Usually the fairer you are, the more sensitive you are to UV,
          the higher risk you might get skin cancer. But everyone can get a
          sunburn, even on a cloudy day. It is found that 80 percent of the UV
          rays can penetrate the clouds. Therefore, sun protection is needed
          whenever you are in sunlight exposure.
        </p>
        <p>
          Be careful if you are the following, because you might be in risk of
          getting a sunburn:
        </p>
        <ul>
          <li class="first-li">Working or doing sports outdoors</li>
          <li class="first-li">Getting repeated sunburn</li>
          <li class="first-li">
            Taking certain medication, including antibiotics such as
            doxycycline, retinoids and heart medications such as diuretics.
          </li>
          <li class="first-li">Currently having dermatitis</li>
        </ul>
        <p>
          If you sweat easily or go for a swim, you should reapply sunscreen
          regularly to reduce the risk of sunburn. Otherwise, reapplying
          sunscreen every two hours is important.
        </p>
      </article>
      <h2 id="sunburn-severity">How severe is sunburn?</h2>
      <article>
        <p>There are three degrees of sunburn, from mild to severe.</p>
        <p>
          First degree sunburn is the damage to the outer layer of the skin
          (epidermis), usually heals on its own in a few days to a week.
        </p>
        <p>Symptoms:</p>
        <ul>
          <li class="first-li">Redness.</li>
          <li class="first-li">Skin feels hot or tight.</li>
          <li class="first-li">Pain or tenderness.</li>
          <li class="first-li">Blistering.</li>
          <li class="first-li">Swelling.</li>
          <li class="first-li">Peeling skin.</li>
        </ul>
        <p>
          Second degree sunburn is the damage to a part of inner layer of the
          skin (dermis). This may cause blistering. It can take weeks to heal
          and may need medical treatment.
        </p>
        <p>Symptoms:</p>
        <ul>
          <li class="first-li">Extremely red skin.</li>
          <li class="first-li">Blistering and swelling over a larger area.</li>
          <li class="first-li">Wet-looking skin.</li>
          <li class="first-li">Pain.</li>
          <li class="first-li">White discoloration within the burn.</li>
        </ul>
        <p>
          Third degree sunburn destroy the epidermis and dermis. Emergency
          treatment is required. Third degree sunburn can happen but it is rare.
        </p>
        <p>Symptoms:</p>
        <ul>
          <li class="first-li">Leathery-looking burn.</li>
          <li class="first-li">Numb skin.</li>
          <li class="first-li">White or dull skin colour.</li>
          <li class="first-li">
            All of the above systemic heat illness symptoms including shock
            and/or heat stroke.
          </li>
        </ul>
      </article>
      <h2 id="protect-self">How do I protect myself?</h2>
      <article>
        <p>
          When the UV index is at 3 or above, Cancer Council Australia
          recommends a combination of sun protection to protect against sun
          damage:
        </p>
        <p>
          <strong>Slip</strong> on some protective clothing and cover the skin
          as much as possible.<br />
          <strong>Slop</strong> on broad spectrum, water resistant SPF30+ or
          higher sunscreen. Not sure how to choose your sunscreen? check here!
          <br /><strong>Slap</strong> on a hat to protect your face, head, neck
          and ears. Seek some shade.<br />
          <strong>Slide</strong> on some sunglasses, making sure they meet
          Australian Standards.
        </p>
        <a href="https://www.healthdirect.gov.au/sun-protection-infographic"
          ><img
            class="sun-protection-img"
            src="@/assets/images/f9c288322263aa0d3177d4fa7f4bc03.png"
            alt="sun protection"
        /></a>
      </article>

