      <article>
        <p>
          Applying sunscreen is one of the best and easiest way to protect
          yourself from the sun. Used regularly, sunscreen helps protect you by
          acting as a barrier to prevent overexposure to the sun’s damaging UV
          rays, which may lead to skin cancer. Therefore, applying sunscreen
          should be part of your daily skincare routine.
        </p>
        <p>
          Now, you know that you should apply sunscreen of at least SPF 30 every
          day to prevent skin cancer, but do you know the kind of sunscreen you
          use is also important? As a matter of fact, choosing a sunscreen can
          be confusing just like many other skincare products. In general, there
          are two distinct types of sunscreens – chemical sunscreens and
          physical sunscreens. Each type shields your skin from the harmful
          sun’s UV rays differently. Here, we will discuss the difference
          between the two, and its own unique pros and cons.
        </p>
      </article>
      <h2 id="physical-sunscreen">Physical Sunscreens</h2>
      <article>
        <p>
          Physical sunscreens, sometimes referred as mineral sunscreens, sit on
          top of the skin, and reflect the sun’s rays. They act as a physical
          barrier by deflecting and scattering UV rays away from your skin.
          Physical sunscreens are formulated with zinc oxide and titanium oxide,
          which can block a wide range of UV wavelengths. Also, physical
          sunscreens are immediately effective as soon as they are applied on
          the skin. Hence, they do not need any waiting time but do take note
          that they need to be reapplied frequently, especially after outdoor
          activities such as hiking and swimming.
        </p>
        <p>
          It’s important to apply physical sunscreens generously and evenly to
          ensure that every single part of your skin, including your neck, ears
          and even your feet is protected from the sun’s rays.
        </p>
        <p>The Pros:</p>
        <ul>
          <li class="first-li">Protect the skin from UVA and UVB rays</li>
          <li class="first-li">Protect the skin from UVA and UVB rays</li>
          <li class="first-li">
            Less irritating than certain chemical sunscreens and suitable for
            those with sensitive skins
          </li>
          <li class="first-li">
            Tend to look white on the skin and hence easy to check where
            physical sunscreens have been applied
          </li>
        </ul>
        <p>The Cons:</p>
        <ul>
          <li class="first-li">
            Can be rubbed off easily after sweating and swimming (required
            frequent reapplication)
          </li>
          <li class="first-li">Not suitable for use under makeup</li>
          <li class="first-li">
            Wrong formula often leaves white cast on the skin
          </li>
        </ul>
      </article>
      <h2 id="chemical-sunscreen">Chemical Sunscreens</h2>
      <article>
        <p>
          In contrast to physical sunscreens, chemical sunscreens contain
          ingredients that are absorbed into the skin. Currently, there are 16
          active ingredients in the chemical sunscreens that are approved by the
          FDA. Below are a few examples of ingredients found in the chemical
          sunscreens:
        </p>
        <ul>
          <li class="first-li">Avobenzone</li>
          <li class="first-li">Homosalate</li>
          <li class="first-li">Octisalate</li>
          <li class="first-li">Oxybenzone</li>
          <li class="first-li">Octinoxate</li>
        </ul>
        <p>
          When the UV rays hit your skin, the sunscreens act like a sponge by
          absorbing the sun’s rays and convert them into heat, then dissipates
          from the skin. This means that you are protected from the UV radiation
          before it penetrates your skin.
        </p>
        <p>The Pros:</p>
        <ul>
          <li class="first-li">
            Tends to be more resistant to water and sweat compared to physical
            sunscreens
          </li>
          <li class="first-li">
            Tends to be more resistant to water and sweat compared to physical
            sunscreens
          </li>
          <li class="first-li">
            Tends to be more resistant to water and sweat compared to physical
            sunscreens
          </li>
        </ul>
        <p>The Cons:</p>
        <ul>
          <li class="first-li">
            Takes time to be effective before exposing to sunlight
          </li>
          <li class="first-li">Not ideal for sensitive skin</li>
        </ul>
      </article>
      <h2 id="choose">Which is Right for You?</h2>
      <article>
        <p>
          In reality, both chemical and physical sunscreens are effective sun
          protection when used properly. Hence, deciding which type to use
          really depends on your personal preference, weather condition, and the
          activities you will be having. If you plan to go hiking or swimming on
          a sunny day, you may want to opt for chemical sunscreens are they
          offer better resistant to water and sweat. On the other hand, physical
          sunscreens are more suited for casual daily activities like spending
          the day at the beach. In the end, find a sunscreen that you feel most
          comfortable with as long as it’s a broad spectrum lotion and at least
          SPF 30.
        </p>
      </article>
